.fade-leave-active {
  @apply transition-opacity;
  @apply duration-300;
}

.slide-left-enter,
.slide-left-leave-to {
  @apply transform;
  @apply translate-x-full;
}

.slide-right-enter,
.slide-right-leave-to {
  @apply transform;
  @apply -translate-x-full;
}

.slide-down-enter,
.slide-down-leave-to {
  @apply opacity-0;
  @apply transform;
  @apply -translate-y-full;
}

.slide-up-enter,
