.header-items-leave-active:nth-child(1) {
  transition-delay: 0ms;
}
.header-items-enter-active:nth-child(2),
.header-items-leave-active:nth-child(2) {
  transition-delay: 100ms;
}
.header-items-enter-active:nth-child(3),
.header-items-leave-active:nth-child(3) {
  transition-delay: 200ms;
}
.header-items-enter-active:nth-child(4),
.header-items-leave-active:nth-child(4) {
  transition-delay: 300ms;
}
.header-items-enter-active:nth-child(5),
.header-items-leave-active:nth-child(5) {
  transition-delay: 400ms;
}
.header-items-enter-active:nth-child(6),
.header-items-leave-active:nth-child(6) {
  transition-delay: 500ms;
}
.header-items-enter-active:nth-child(7),
.header-items-leave-active:nth-child(7) {
  transition-delay: 600ms;
}
.header-items-enter-active:nth-child(8),
.header-items-leave-active:nth-child(8) {
  transition-delay: 700ms;
}
.header-items-enter-active:nth-child(9),
.header-items-leave-active:nth-child(9) {
  transition-delay: 800ms;
}
.header-items-enter-active:nth-child(10),
.header-items-leave-active:nth-child(10) {
  transition-delay: 900ms;
}
.header-items-enter-active:nth-child(11),
.header-items-leave-active:nth-child(11) {
  transition-delay: 1000ms;
}

.header-items-enter,
.header-items-leave-to {
  opacity: 0;
  transform: translateX(-1em);
}

@screen sm {
  [data-aos=appear] {
    transform: translateY(10px);
    opacity: 0;
    transition-property: transform, opacity;
  }
  [data-aos=appear].aos-animate {
    transform: translateY(0);
    opacity: 1;
  }
}

